	module.exports = {"data":{"markdownRemark":{"html":"<p>夜中、カラスのぎゃあぎゃあ喚く声で目を覚ましてしまう。時計を見ると2:30ジャスト、酷く気味が悪い。明け方なら分からないでもないが夜中に、しかもここまでぎゃあぎゃあ喚いてるのがまずありえない。物の怪か、あるいは大災害の予兆の蟲の知らせか…一度湧いた恐怖はなかなか離れず、まるで身を守るかのように頭まで布団に篭る。もし仮に物の怪に襲われたら布団など何の役にも立たぬだろうと判っているが(性的な意味除く)。改めて時計を見るとカチコチ音は鳴っているものの、微かに振れるのみで回っていない秒針……まるで02:30に呪われているかのように。 ……突如、脳裏に物の怪・災害予兆に次ぐ第三の恐怖がよぎりベッドから飛び出しロフトから駆け降り、そして物凄い勢いで窓を開けた…… ──真っ白く輝く曇り空。振り返るとロフト下にあるデジタル時計は8:25を回ろうとしているところだった。</p>\n<ul>\n<li>ヒント：休出(休日出勤日)</li>\n<li>さらにヒント：書きたくない</li>\n</ul>","frontmatter":{"cover":null,"date":"2006-06-17T08:38:00+09:00","strDate":"2006/6/16","title":"02:30の呪い、夜中のカラス"}}},"pathContext":{"path":"/ja/blog/2006/06/17-0230の呪い、夜中のカラス/","langKey":"ja"}}

